.creo__contact {
    display: flex;
    flex-direction: row;
}

.creo__contact-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;
}

.creo__contact-image img {
    width: 100%;
    height: 100%;
    border-radius: 15em;
}

.creo__contact-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}

.creo__contact-content h4 {
    font-family: var(--font-family);
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text)
}

.creo__contact-content h4:last-child {
    color: #FF8A71;
}

.creo__contact-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;

    margin: 1rem 0;
}

.creo__contact-content P {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);

    margin-bottom: 2rem;
}

/* .creo__contact-content h4 {
    text-indent: 1em;
} */
@media screen and (max-width: 1250px) {
    .creo__contact {
        flex-direction: column;
    }

    .creo__contact-image {
        margin: 0.5rem 0;
    }

    .creo__contact-content {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 950px) {
    .creo__contact {
        flex-direction: column;
    }

    .creo__contact-image {
        margin: 1rem 0;
    }

    .creo__contact-content {
        margin-top: 2rem;
    }
}

@media screen and (max-width:550px) {
    .creo__contact-image {
        margin:2em;
    }
}