.creo__contactform {
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

.creo__contactform-label {
    width: 100%;
    display: flex;
    color: var(--color-subtext);
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    font-size: 1.2em;
    line-height: 1.2em;
    font-weight: 1000;
    font-style: oblique;
    /* margin: 1rem; */
    padding:1em 0;
    /* margin-right: 10em; */
}

.creo__contactform-input {
    min-width: 300px;
    height: 40px;
    background-color: var(--color-footer);
    color: var(--color-text);
    border: none;
}

.creo__contactform-message_textarea {
    min-width: 300px;
    min-height: 20em;
    max-width: 700px;
    max-height: 30em;
    border-radius: 0.8em;
    padding: 0.9em;
}

.creo__contactform-btn {
    margin-top: 2em;
    padding: 1em;
    border-radius: 0.8em;
    font-family: var(--font-family);
    background-color: var(--color-text);
    font-size: 14px;
    font-weight: 800;
    text-transform: uppercase;
    cursor: pointer;
}