.creo__fonctionnement-container_article {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.creo__fonctionnement-container_article:hover {
    background-color:#10467c;
}

.creo__fonctionnement-container_article:hover .creo__fonctionnement-container_article-content {
    animation: wow_effect-div 500ms forwards alternate;
}

.creo__fonctionnement-container_article:hover .creo__fonctionnement-container_article-content__t01 {
    animation: wow_effect-t01 500ms forwards alternate;
}


.creo__fonctionnement-container_article:hover .creo__fonctionnement-container_article-content__t02 {
    animation: wow_effect-t02 500ms forwards alternate;
}

@keyframes wow_effect-div {
    to {
        transform: translateY(1%);
        border-radius: 0 10px 10px 10px;
    }
}

@keyframes wow_effect-t01 {
    to {
       background-color: var(--color-subtext);
       text-shadow: 1px 1px 2px rgb(0, 0, 0), 0 0 1em rgb(0, 0, 0), 0 0 0.2em rgb(0, 0, 0);
       color:rgb(207, 219, 249);
       border-radius: 1rem;
       padding:1rem;
    }
   }

@keyframes wow_effect-t02 {
 to {
    color: rgb(207, 219, 249);
 }
}

.creo__fonctionnement-container_article-image {
    width: 100%;
    height: 111.66px;
    background: var(--color-bg);
    display: grid;
    grid-auto-flow :column;
    place-content: center;
}

.creo__fonctionnement-container_article-image img {
    width: 100%;
    height: 111.66px;
    --m:
    radial-gradient(circle farthest-side at right,#000 99%,#0000) 
      0 100%/46% 100% no-repeat,
    radial-gradient(circle farthest-side at left ,#000 99%,#0000) 
      100% 0/46% 100% no-repeat;
    -webkit-mask: var(--m);
            mask: var(--m);
    filter: grayscale(.5);
    transition: .3s linear;
    cursor: pointer;
}

.creo__fonctionnement-container_article:hover .creo__fonctionnement-container_article-image img {
    -webkit-mask-position: 7.5% 50%,92.5% 50%;
    mask-position: 7.5% 50%,92.5% 50%;
    filter: grayscale(0);
}

.creo__fonctionnement-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
}

.creo__fonctionnement-container_article-content p, .creo__fonctionnement-container_article-content h3 {
    font-family: var(--font-family);
    text-shadow: 1px 1px 2px rgb(122, 44, 44), 0 0 1em rgb(40, 46, 121), 0 0 0.2em rgb(0, 0, 0);
}

.creo__fonctionnement-container_article-content__etape {
    font-size: 11.65px;
    font-weight: 700;
    line-height: 35px;
    color: #fff;
}

.creo__fonctionnement-container_article-content__t01 {
    color: var(--color-subtext);

    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.30px;
    text-shadow: 1px 1px 2px rgb(122, 44, 44), 0 0 1em rgb(40, 46, 121), 0 0 0.2em rgb(0, 0, 0);
    text-align: center;
    text-transform:uppercase ;
    
    margin-bottom: 5rem;
    cursor: pointer;
}

.creo__fonctionnement-container_article-content__t02 {
    text-align: center;
    color: var(--color-text);
    font-size: 18px;
    font-weight: 400;
    line-height: 30.30px;
    cursor: pointer;
    white-space: pre-line;
}

.creo__fonctionnement-container_article-content p:last-child {
    cursor: pointer;
}


@media screen and (max-width: 550px) {
    .creo__fonctionnement-container_article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}
