.creo__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.creo__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.creo__navbar-links_logo {
    margin-right: 2rem;
}

.creo__navbar-links_logo img {
    width: 250.24px;
    height: 240.32px;
}

.creo__navbar-links_container {
    display: flex;
    flex-direction: row;
    
    margin-left: 20px;
}

.creo__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    display:none;
}

.creo__navbar-links_container p,
.creo__navbar-sign p,
.creo__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.creo__navbar-sign button,
.creo__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border:0;
    outline: none;
    cursor: pointer;
}

.creo__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.creo__navbar-menu png {
    cursor: pointer;
}

.creo__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align:left;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.creo__navbar-menu_container p {
    margin: 1rem 0;
}

.creo__navbar-menu_container-links-sign {
    display: none;
}

@media screen and (max-width: 1050px) {
    .creo__navbar-links_logo img {
        position: absolute;
        left:35%;
        margin-bottom: 20rem;
    }
    
    .creo__navbar-links_container {
        display: none;
    }

    .creo__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .creo__navbar {
        padding: 2rem;
    }
}

@media screen and (max-width: 550px) {
    .creo__navbar-links_logo img {
        position:absolute;
        left:35%;
        top:0.1%;
        height:120.16px;
        width:125.12px;
    }

    .creo__navbar {
        padding: 2rem;
    }

    .creo__navbar-sign {
        display: none;
    }

    .creo__navbar-menu_container {
        top: 20px;
        font-size: 12px;
        line-height: 14px;
        padding-right:1.5rem;
        margin: 0;
    }

    .creo__navbar-menu_container-links-sign {
        /*display: block;*/
        display: none;
    }
}