.creo__cta {
    border: 1px solid white;
    color: white;

    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar);

}

.creo__cta-content p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    color: #0E0E0E;
}

.creo__cta-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000;
}

.creo__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.creo__cta-btn button {
    background-color: #000;
    color: #fff;
    border-radius: 40px;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width: 650px) {
    .creo__cta {
        flex-direction: column;
    }

    .creo__cta-btn {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 650px) {
    .creo__cta {
        margin: 4rem 2rem;
    }

    .creo__cta-content h3 {
        font-size: 14px;
        line-height: 32px; 
    }

    .creo__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
}